import React, {useEffect,useRef,useState} from 'react';
import {Link,useParams, Router} from 'react-router-dom';
import i18n from "i18next";
import { useTranslation, initReactI18next } from "react-i18next";
import Sidebar from '../../component/Sidebar';
import Header from '../../component/Header';
import MaterialTable from 'material-table';
import download from '../../images/download.png'
import preveiw from '../../images/preveiw.png'
import { API_URL } from '../../../config';

const Recording = () => {
    
    const { t } = useTranslation();
    const accessToken = localStorage.getItem('accessToken');
    const [recordings, setrecordings] = useState([]);
    const [data, setData] = useState([]);


    useEffect(() =>{
        Recordings();

    },[]);

    const Recordings = () =>{

        fetch(API_URL+"/recording/distributor",
        
               {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json',
                    'x-access-token': accessToken,
                },
               }
        
        ).then((response) => {
            if (response.status == 200) {
                response.json().then((resp) => {
                    console.warn("result", resp);
                    let _temp = [];
                    resp.recordings.map((v,i) => {
                        _temp.push({
                            recordingname: v.recording_name,
                            recordingtype: v.recording_type,
                            status: v.status == 1 ? "Active" : "Inactive",
                            action : <p> <a href='#' className="downloadimg"><img src={preveiw} /></a></p>
                        })
                    })
                    setData(_temp);
                   

                });
            }
            else if (response.status == 401) {
                logout()
            }
            else {
                alert("network error")
            }


        })


    }

    const logout = () => {
        localStorage.clear();
        window.location.reload();
    }

    
    const columns =[
        {
            title: t('Recording-Name'), field: "recordingname"
        },
        {
            title: t('Recording-Type'), field: "recordingtype"
        },
        {
            title: t('Status'), field: "status"
        },
        {
            title: <span className="text-right">{t('Actions')}</span>, field: "action"
        }
    ]


    return(
        <div className="">
            <Header />
             <div className="wrp-dashbord">
                <div className="sidebar-section">
                <Sidebar />
               </div>
               <div className="right-section">
                <div className="head-demoreport">
                    <h3>{ t('Recordings')}</h3>
                </div>
                <div className="wrp-bankform">
                    <div style={{ maxWidth: '100%' }}>
                        <MaterialTable
                        columns={columns}
                        data={data}
                        title=""
                        />
                        
                    </div>
                </div>
               </div>
             </div>
            
        </div>
    )
}

export default Recording;